import React, { FC, useState } from "react";
import { RevealOnScroll } from "../components/ui/RevealOnScroll";

// Define the type for our venture items
interface VentureItem {
  id: string;
  name: string;
  description: string;
  logo: string;
  url?: string;
}

export const Ventures: FC = () => {
  // State to track which card is being hovered
  const [hoveredCard, setHoveredCard] = useState<string | null>(null);

  // Define the venture items
  const ventures: VentureItem[] = [
    {
      id: "hey-im-samir",
      name: "Hey I'm Samir",
      description: "I drive business impact in fintech",
      logo: "/assets/Hey I'm Samir 2025.png",
      url: "/about"
    },
    {
      id: "interspace",
      name: "Interspace",
      description: "Over-engineered posts on fintech, stratfin and unit economics nerdery",
      logo: "/assets/Interspace Square - 2025.png",
      url: "https://interspace.samir.xyz/"
    },
    {
      id: "perspectives",
      name: "Perspectives",
      description: "Over-engineered perspectives on tech, design, food, art, travel and life as we know it.",
      logo: "/assets/Perspectives.png",
      url: "https://perspectives.samir.xyz/"
    },
    {
      id: "predictive",
      name: "Predictive",
      description: "Predictive algorithm for academy awards",
      logo: "/assets/Predictive.film icon 2025.png",
      url: "https://predictive.film/"
    },
    {
      id: "solo",
      name: "Solo",
      description: "Beautifully simple climbing app",
      logo: "/assets/Solo Logo 2025 Square.png",
      url: "https://solo.app/"
    },
    {
      id: "2de",
      name: "2 Days Early",
      description: "Community of current and former Chime operators that invest in fintech",
      logo: "/assets/2DE Interspace.png",
      url: "https://2daysearly.com/"
    }
  ];

  return (
    <div className="space-y-8">
      {/* Page Header */}
      <div className="mb-8">
        <h1 className="text-5xl sm:text-6xl font-bold">Interspace Ventures</h1>
        <p className="text-xl sm:text-2xl max-w-3xl mt-4">
          I write, design, code and build ventures with over-engineered simplicity and artificial intelligence.
        </p>
      </div>

      {/* Ventures Grid */}
      <RevealOnScroll>
        <div className="grid grid-cols-2 sm:grid-cols-3 gap-0 border-0 outline-none shadow-none" style={{ borderSpacing: 0, borderCollapse: 'collapse' }}>
          {ventures.map((venture) => (
            <div
              key={venture.id}
              className="relative overflow-hidden transition-all duration-300 aspect-square cursor-pointer transform hover:scale-[1.02]"
              style={{ padding: 0, margin: 0, border: 'none', outline: 'none', boxShadow: 'none', borderRadius: 0 }}
              onMouseEnter={() => setHoveredCard(venture.id)}
              onMouseLeave={() => setHoveredCard(null)}
              onClick={() => venture.url && window.open(venture.url, venture.url.startsWith('/') ? '_self' : '_blank')}
            >
              {/* Logo */}
              <div className="w-full h-full flex items-center justify-center" style={{ padding: 0, margin: 0 }}>
                <img 
                  src={venture.logo}
                  alt={venture.name}
                  className={`object-contain transition-all duration-300 ${
                    hoveredCard === venture.id ? 'scale-90 opacity-30' : 'scale-100'
                  }`}
                  style={{ maxWidth: '85%', maxHeight: '85%', padding: 0, margin: 0 }}
                />
              </div>
              
              {/* Overlay on hover */}
              <div 
                className={`absolute inset-0 bg-black bg-opacity-80 flex flex-col items-center justify-center transition-all duration-300 ${
                  hoveredCard === venture.id ? 'opacity-100 scale-100' : 'opacity-0 scale-95'
                } md:hover:opacity-100 md:hover:scale-100`}
                style={{ padding: '1rem', margin: 0, border: 'none' }}
              >
                <h3 className="text-lg sm:text-xl font-bold mb-2 text-white">{venture.name}</h3>
                <p className="text-sm text-center text-white max-w-[90%]">
                  {venture.description}
                </p>
              </div>
            </div>
          ))}
        </div>
      </RevealOnScroll>
    </div>
  );
};

export default Ventures;